import React, { useState, useContext, useCallback } from 'react';
import axios from 'axios';
import moment from 'moment';
import 'moment/locale/pt-br';
import Context from '../Context';
import { Doughnut, Line } from 'react-chartjs-2'
import ChartDataLabels from 'chartjs-plugin-datalabels'

// import setaesquerda from '../images/arrowleft.svg';
// import setadireita from '../images/arrowright.svg';

function AptPlanoTerapeutico() {
  // recuperando estados globais (Context.API).
  const {
    idatendimento
  } = useContext(Context)
  var html = 'https://pulsarapp-server.herokuapp.com';

  const [data, setdata] = useState(moment().format('DD/MM/YYYY'))
  const [valor, setvalor] = useState();
  const [adf, setadf] = useState();
  const [adfestabelecido, setadfestabelecido] = useState();
  const [adfbasica, setadfbasica] = useState();
  const [adfinstrumental, setadfinstrumental] = useState();
  const [crf, setcrf] = useState();
  const [crfpctefragil, setcrfpctefragil] = useState();
  const [ddfecognicao, setddfecognicao] = useState();
  const [ddfehumor, setddfehumor] = useState();
  const [ddfemobilidade, setddfemobilidade] = useState();
  const [ddfecomunicacao, setddfecomunicacao] = useState();
  const [linhasdecuidados, setlinhasdecuidados] = useState();
  const [metasterapeuticas, setmetasterapeuticas] = useState(); // será uma array, um código para abertura de conjunto de metas?

  const [planoterapeutico, setplanoterapeutico] = useState([])
  const loadPlanoTerapeutico = () => {
    axios.get(html + "/planoterapeutico").then((response) => {
      var x = [0, 1];
      x = response.data;
      setplanoterapeutico(x.filter(item => item.idatendimento == idatendimento));
    });
  }

  function ListaDePlanosTerapeuticos() {
    return (
      <div className="scroll">
        {planoterapeutico.map(item => (
          <div className="row">
            <button onClick={() => selectPlanoTerapeutico(item)}>{item.data}</button>
          </div>
        ))}
      </div>
    )
  }

  const selectPlanoTerapeutico = (item) => {
    setdata(item.data);
    setvalor(item.valor);
    setadf(item.adf);
    setadfestabelecido(item.adfestabelecido);
    setcrf(item.crf);
    setcrfpctefragil(item.crfpctefragil);
    setddfecognicao(item.ddfecognicao);
    setddfehumor(item.ddfehumor);
    setddfemobilidade(item.ddfemobilidade);
    setddfecomunicacao(item.ddfecomunicacao);
    setlinhasdecuidados(item.linhasdecuidados);
    setmetasterapeuticas(item.metasterapeuticas);
  }

  const createPlanoTerapeutico = () => {
    var obj = {
      idatendimento: idatendimento,
      data: data,
      valor: valor,
      adf: adf,
      adfestabelecido: adfestabelecido,
      crf: crf,
      crfpctefragil: crfpctefragil,
      ddfecognicao: ddfecognicao,
      ddfehumor: ddfehumor,
      ddfemobilidade: ddfemobilidade,
      ddfecomunicacao: ddfecomunicacao,
      linhasdecuidados: linhasdecuidados,
      metasterapeuticas: metasterapeuticas,
    }
    axios.post(html + '/createplanoterapeutico', obj).then(() => {
    });
  }

  const updatePlanoTerapeutico = (item) => {
    var obj = {
      idatendimento: idatendimento,
      data: data,
      valor: valor,
      adf: adf,
      adfestabelecido: adfestabelecido,
      crf: crf,
      crfpctefragil: crfpctefragil,
      ddfecognicao: ddfecognicao,
      ddfehumor: ddfehumor,
      ddfemobilidade: ddfemobilidade,
      ddfecomunicacao: ddfecomunicacao,
      linhasdecuidados: linhasdecuidados,
      metasterapeuticas: metasterapeuticas,
    }
    axios.post(html + '/updateplanoterapeutico/' + item.id, obj).then(() => {
    });
  }

  const deletePlanoTerapeutico = (item) => {
    axios.get(html + "/deleteplanoterapeutico/'" + item.id + "'").then(() => {
    });
  }

  function Regua() {
    return (
      <div id="ESCALA">
        <div id="setas fragilidade x vitalidade"
          style={{
            padding: 5, margin: 5, backgroundImage: 'linear-gradient(to right, #ec7063, #f5b041, #52be80)',
            display: 'flex', flexDirection: 'row', justifyContent: 'space-between', borderRadius: 5,
            height: 25,
          }}
        >
          <div id="fragilidade"
            style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center' }}
          >
            <div className="title2" style={{ color: 'white', height: 30, textShadow: '0px 0px 3px black' }}>{'FRAGILIDADE'}</div>
          </div>
          <div id="vitalidade"
            style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center' }}
          >
            <div className="title2" style={{ color: 'white', height: 30, textShadow: '0px 0px 3px black' }}>{'VITALIDADE'}</div>
          </div>
        </div>
        <div id="regua"
          style={{
            width: '100%', padding: 5, margin: 0,
            display: 'flex', flexDirection: 'row', justifyContent: 'space-evenly',
          }}
        >
          <button className="blue-button">
            1
          </button>
          <button className="blue-button">
            2
          </button>
          <button className="blue-button">
            3
          </button>
          <button className="blue-button">
            4
          </button>
          <button className="blue-button">
            5
          </button>
          <button className="blue-button">
            6
          </button>
          <button className="blue-button">
            7
          </button>
          <button className="blue-button">
            8
          </button>
          <button className="blue-button">
            9
          </button>
          <button className="blue-button">
            10
          </button>
        </div>
      </div>
    )
  }

  function AvaliacaoDeDeclinioFuncional() {
    return (
      <div id="AVALIAÇÃO DE DECLÍNIO FUNCIONAL - ADF">
        <div className="title2" style={{ margin: 0 }}>AVALIAÇÃO DE DECLÍNIO FUNCIONAL</div>
        <div id="ADF - opções" style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center', padding: 5, marginTop: 0 }}>
          <button style={{ width: '8vw', height: 90, opacity: adf == 1 ? 1 : 0.3 }} className="green-button" onClick={() => setadf(1)}>
            AUSENTE
          </button>
          <button style={{ width: '9vw', height: 90, opacity: adf == 2 ? 1 : 0.3 }} className="yellow-button" onClick={() => setadf(2)}>
            IMINENTE
          </button>
          <button style={{ width: adf != 3 ? '9vw' : '20vw', height: 90, opacity: adf == 3 ? 1 : 0.3 }} className="red-button" onClick={() => setadf(3)}>
            <div>ESTABELECIDO</div>
            <div id="ADF - opções - estabelecido" style={{ display: adf == 3 ? 'flex' : 'none', justifyContent: 'space-evenly' }}>
              <button className="softred-button"
                style={{
                  maxHeight: 40,
                  borderColor: adfbasica == 1 ? 'white' : 'transparent', borderWidth: 3,
                  borderStyle: 'solid', width: '9vw', fontSize: 12, padding: 5, opacity: adfbasica == 1 ? 1 : 0.5
                }}
                onClick={adfbasica == 1 ? () => setadfbasica(0) : () => setadfbasica(1)}>
                AVD BÁSICA
              </button>
              <button className="softred-button"
                style={{
                  maxHeight: 40,
                  borderColor: adfinstrumental == 1 ? 'white' : 'transparent', borderWidth: 3,
                  borderStyle: 'solid', width: '9vw', fontSize: 12, padding: 5, opacity: adfinstrumental == 1 ? 1 : 0.5,
                }}
                onClick={adfinstrumental == 1 ? () => setadfinstrumental(0) : () => setadfinstrumental(1)}>
                AVD INSTRUMENTAL
              </button>
            </div>
          </button>
        </div>
      </div>
    )
  }

  function ClassificacaoDeRiscoFuncional() {
    return (
      <div id="CLASSIFICAÇÃO DE RISCO FUNCIONAL - CRF">
        <div className="title2" style={{ margin: 0 }}>CLASSIFICAÇÃO DE RISCO FUNCIONAL</div>
        <div id="CRF - opções" style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center', padding: 5, marginTop: 0 }}>
          <button style={{ width: '8vw', height: 90, opacity: crf == 1 ? 1 : 0.3 }} className="green-button" onClick={() => setcrf(1)}>
            PACIENTE ROBUSTO
          </button>
          <button style={{ width: '9vw', height: 90, opacity: crf == 2 ? 1 : 0.3, maxHeight: 200 }} className="yellow-button" onClick={() => setcrf(2)}>
            RISCO DE FRAGILIZAÇÃO
          </button>
          <button style={{ width: crf != 3 ? '9vw' : '20vw', height: 90, opacity: crf == 3 ? 1 : 0.3 }} className="red-button" onClick={() => setcrf(3)}>
            PACIENTE FRÁGIL
            <div id="CRF - opções - paciente frágil" style={{ display: crf == 3 ? 'flex' : 'none', justifyContent: 'space-evenly' }}>
              <button
                style={{
                  width: '9vw', fontSize: 12, padding: 5, opacity: crfpctefragil == 1 ? 1 : 0.5,
                  borderColor: crfpctefragil == 1 ? 'white' : 'transparent', borderWidth: 3,
                  borderStyle: 'solid', width: '9vw'
                }}
                className="softred-button" onClick={() => setcrfpctefragil(1)}>
                BAIXA COMPLEXIDADE
              </button>
              <button
                style={{
                  width: '45%', fontSize: 12, padding: 5, opacity: crfpctefragil == 2 ? 1 : 0.5,
                  borderColor: crfpctefragil == 2 ? 'white' : 'transparent', borderWidth: 3,
                  borderStyle: 'solid', width: '9vw'
                }}
                className="softred-button" onClick={() => setcrfpctefragil(2)}>
                ALTA COMPLEXIDADE
              </button>
            </div>
          </button>
        </div>
      </div>
    )
  }

  function DeterminantesDoDeclinioFuncionalEstabelecido() {
    return (
      <div id="DETERMINANTES DO DECLÍNIO FUNCIONAL ESTABELECIDO - DDFE" style={{ display: adf == 3 ? 'flex' : 'none', flexDirection: 'column', justifyContent: 'center' }}>
        <div className="title2" style={{ margin: 0, marginBottom: 10 }}>DETERMINANTES DO DECLÍNIO FUNCIONAL ESTABELECIDO</div>
        <div id="DDFE - opções" style={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-evenly', padding: 5, margin: -10 }}>
          <div className="card" style={{ margin: 0, marginLeft: 5, marginRight: 5, flexDirection: 'column', justifyContent: 'center', width: '15vw' }}>
            <div className="title2center" style={{ fontSize: 14, height: 50 }}>COGNIÇÃO</div>
            <div id="DFE - opções - cognição" style={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-evenly', width: '100%', marginBottom: 10 }}>
              <button className={ddfecognicao == 1 ? "red-button" : "blue-button"} onClick={() => setddfecognicao(1)}>
                L
              </button>
              <button className={ddfecognicao == 2 ? "red-button" : "blue-button"} onClick={() => setddfecognicao(2)}>
                M
              </button>
              <button className={ddfecognicao == 3 ? "red-button" : "blue-button"} onClick={() => setddfecognicao(3)}>
                G
              </button>
            </div>
          </div>
          <div className="card" style={{ margin: 0, marginLeft: 5, marginRight: 5, flexDirection: 'column', justifyContent: 'center', width: '15vw' }}>
            <div className="title2center" style={{ fontSize: 14, height: 50 }}>HUMOR E COMPORTAMENTO</div>
            <div id="DFE - opções - humor e comportamento" style={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-evenly', width: '100%', marginBottom: 10 }}>
              <button className={ddfehumor == 1 ? "red-button" : "blue-button"} onClick={() => setddfehumor(1)}>
                L
              </button>
              <button className={ddfehumor == 2 ? "red-button" : "blue-button"} onClick={() => setddfehumor(2)}>
                M
              </button>
              <button className={ddfehumor == 3 ? "red-button" : "blue-button"} onClick={() => setddfehumor(3)}>
                G
              </button>
            </div>
          </div>
          <div className="card" style={{ margin: 0, marginLeft: 5, marginRight: 5, flexDirection: 'column', justifyContent: 'center', width: '15vw' }}>
            <div className="title2center" style={{ fontSize: 14, height: 50 }}>MOBILIDADE</div>
            <div id="DFE - opções - mobilidade" style={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-evenly', width: '100%', marginBottom: 10 }}>
              <button className={ddfemobilidade == 1 ? "red-button" : "blue-button"} onClick={() => setddfemobilidade(1)}>
                L
              </button>
              <button className={ddfemobilidade == 2 ? "red-button" : "blue-button"} onClick={() => setddfemobilidade(2)}>
                M
              </button>
              <button className={ddfemobilidade == 3 ? "red-button" : "blue-button"} onClick={() => setddfemobilidade(3)}>
                G
              </button>
            </div>
          </div>
          <div className="card" style={{ margin: 0, marginLeft: 5, marginRight: 5, flexDirection: 'column', justifyContent: 'center', width: '15vw' }}>
            <div className="title2center" style={{ fontSize: 14, height: 50 }}>COMUNICAÇÃO</div>
            <div id="DFE - opções - comunicação" style={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-evenly', width: '100%', marginBottom: 10 }}>
              <button className={ddfecomunicacao == 1 ? "red-button" : "blue-button"} onClick={() => setddfecomunicacao(1)}>
                L
              </button>
              <button className={ddfecomunicacao == 2 ? "red-button" : "blue-button"} onClick={() => setddfecomunicacao(2)}>
                M
              </button>
              <button className={ddfecomunicacao == 3 ? "red-button" : "blue-button"} onClick={() => setddfecomunicacao(3)}>
                G
              </button>
            </div>
          </div>
        </div>
      </div>
    )
  }

  function LinhaDeCuidados() {
    return (
      <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center' }}>
        <div className="title2" style={{ margin: 0, marginTop: 10 }}>LINHA DE CUIDADOS</div>
        <div id="LINHA DE CUIDADOS" style={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-evenly', paddingLeft: 7.5, paddingRight: 7.5 }}>
          <button style={{ width: '22vw' }} className={linhasdecuidados == 1 ? "red-button" : "blue-button"} onClick={() => setlinhasdecuidados(1)}>
            REABILITAÇÃO
          </button>
          <button style={{ width: '22vw' }} className={linhasdecuidados == 2 ? "red-button" : "blue-button"} onClick={() => setlinhasdecuidados(2)}>
            CUIDADOS CRÔNICOS
          </button>
          <button style={{ width: '22vw' }} className={linhasdecuidados == 3 ? "red-button" : "blue-button"} onClick={() => setlinhasdecuidados(3)}>
            CUIDADOS PALIATIVOS
          </button>
        </div>
      </div>
    )
  }

  // obtendo as metas relacionadas ao atendimento.
  const [metas, setmetas] = useState([]);
  const loadMetas = () => {
    axios.get(html + "/metas").then((response) => {
      var x = [0, 1];
      x = response.data;
      setmetas(x);
    })
  };


  function dataChartMetas(item) {
    return (
      {
        datasets: [
          {
            data: [moment().diff(moment(JSON.stringify(item.data).substring(1, 10), 'DD/MM/YYYY')), moment()],
            backgroundColor: ['#ec7063', '#52be80'],
            borderColor: '#ffffff',
            hoverBorderColor: ['#ffffff', '#ffffff'],
          },
        ],
      });
  }

  const getMetas = (item) => {
    return (
      <div className="card" style={{ margin: 5, display: 'flex', flexDirection: 'column', justifyContent: 'flex-start' }}>
        <div id="ITEM DE META"
          style={{
            display: 'flex', flexDirection: 'row', width: '100%',
            justifyContent: 'space-between', alignItems: 'center', marginBottom: 10,
          }}>
          <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center', alignItems: 'center' }}>
            <Doughnut
              data={dataChartMetas(item)}
              width={window.innerWidth > 400 ? 75 : 200}
              height={window.innerWidth > 400 ? 75 : 200}
              plugins={ChartDataLabels}
              options={{
                cutoutPercentage: 40,
                plugins: {
                  legend: {
                    display: false
                  },
                  datalabels: {
                    display: false
                  }
                },
                tooltips: {
                  enabled: false,
                },
                hover: { mode: null },
                elements: {
                  arc: {
                    hoverBorderColor: '#E1E5F2',
                    borderColor: '#E1E5F2',
                    borderWidth: 5,
                    width: 50
                  },
                },
                animation: {
                  duration: 500,
                },
                title: {
                  display: false,
                  text: 'OCUPAÇÃO DE LEITOS',
                },
                legend: {
                  display: false,
                  position: 'bottom',
                },
                maintainAspectRatio: true,
                responsive: false,
              }}
            />
            <div className="title2">
              {'DESCRIÇÃO DA META...'}
            </div>
          </div>
          <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center' }}>
            <div className="yellow-button" style={{ width: 200 }}>
              EM ANDAMENTO
            </div>
            <div title="AVALIAÇÃO DO RESULTADO" className="blue-button"
              style={{ padding: 10, width: 50, opacity: 0.2 }}>
              0
            </div>
          </div>
        </div>
        <div className="title2" style={{ textAlign: 'left', alignSelf: 'flex-start' }}
          title="PROPOSTAS TERAPÊUTICAS PARA O ALCANCE DA META">PROPOSTAS TERAPÊUTICAS:</div>
        <div className="scroll" style={{ height: 100, maxHeight: 100 }}>
          <div className="blue-button">PROPOSTA TERAPÊUTICA 01</div>
          <div className="blue-button">PROPOSTA TERAPÊUTICA 02</div>
          <div className="blue-button">PROPOSTA TERAPÊUTICA 03</div>
        </div>
      </div>
    )
  }

  const dataChartMetas1 = {
    datasets: [
      {
        data: [95, 15],
        backgroundColor: ['#ec7063', '#52be80'],
        borderColor: '#ffffff',
        hoverBorderColor: ['#ffffff', '#ffffff'],
      },
    ],
  }
  const dataChartMetas2 = {
    datasets: [
      {
        data: [100, 0],
        backgroundColor: ['#ec7063', '#52be80'],
        borderColor: '#ffffff',
        hoverBorderColor: ['#ffffff', '#ffffff'],
      },
    ],
  }
  const dataChartMetas3 = {
    datasets: [
      {
        data: [0, 100],
        backgroundColor: ['#ec7063', '#52be80'],
        borderColor: '#ffffff',
        hoverBorderColor: ['#ffffff', '#ffffff'],
      },
    ],
  }

  function MetasTerapeuticas() {
    return (
      <div id="METAS" style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center' }}>

        <div id="OBJETIVOS" className="title4">OBJETIVOS</div>
        <div className="corprincipal"
          style={{
            width: '100%',
            display: 'flex', flexDirection: 'row', justifyContent: 'flex-start',
            margin: 5, marginBottom: 0,
            padding: 5, borderRadius: 5, borderBottomLeftRadius: 0, borderBottomRightRadius: 0,
          }}>
          <div className="title5" style={{ fontSize: 14, alignSelf: 'center', width: 250 }}>
            OBJETIVO PRIMÁRIO:
          </div>
          <div className="hover-button" style={{ width: '100%' }}>
            {'DESCRIÇÃO DO OBJETIVO PRIMÁRIO'}
          </div>
        </div>
        <div id="METAS" className="scroll"
          style={{
            height: 170, margin: 5, marginTop: 0,
            borderTopLeftRadius: 0, borderTopRightRadius: 0,
            backgroundColor: '#ffffff', borderColor: '#ffffff'
          }} >
          <div id="ITEM DE META" className="card" style={{ display: 'flex', flexDirection: 'column', justifyContent: 'flex-start' }}>
            <div
              style={{
                display: 'flex', flexDirection: 'row', width: '100%',
                justifyContent: 'space-between', alignItems: 'center',
              }}>
              <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center', alignItems: 'center' }}>
                <Doughnut
                  data={dataChartMetas1}
                  width={window.innerWidth > 400 ? 75 : 200}
                  height={window.innerWidth > 400 ? 75 : 200}
                  plugins={ChartDataLabels}
                  options={{
                    cutoutPercentage: 40,
                    plugins: {
                      legend: {
                        display: false
                      },
                      datalabels: {
                        display: false
                      }
                    },
                    tooltips: {
                      enabled: false,
                    },
                    hover: { mode: null },
                    elements: {
                      arc: {
                        hoverBorderColor: '#E1E5F2',
                        borderColor: '#E1E5F2',
                        borderWidth: 5,
                        width: 50
                      },
                    },
                    animation: {
                      duration: 500,
                    },
                    title: {
                      display: false,
                      text: 'OCUPAÇÃO DE LEITOS',
                    },
                    legend: {
                      display: false,
                      position: 'bottom',
                    },
                    maintainAspectRatio: true,
                    responsive: false,
                  }}
                />
                <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'flex-start', alignItems: 'flex-start' }}>
                  <div className="title2">
                    {'DESCRIÇÃO DA META...'}
                  </div>
                  <div className="title2">
                    {'VENCIMENTO: 15/11/2021 - TEMPO RESTANTE: 5 DIAS'}
                  </div>
                </div>
              </div>
              <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center' }}>
                <div className="yellow-button" style={{ width: 200 }}>
                  EM ANDAMENTO
                </div>
                <div title="AVALIAÇÃO DO RESULTADO" className="blue-button"
                  style={{ padding: 10, width: 50, opacity: 0.2 }}>
                  0
                </div>
              </div>
            </div>
          </div>
          <div id="ITEM DE META" className="card" style={{ display: 'flex', flexDirection: 'column', justifyContent: 'flex-start' }}>
            <div
              style={{
                display: 'flex', flexDirection: 'row', width: '100%',
                justifyContent: 'space-between', alignItems: 'center',
              }}>
              <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center', alignItems: 'center' }}>
                <Doughnut
                  data={dataChartMetas2}
                  width={window.innerWidth > 400 ? 75 : 200}
                  height={window.innerWidth > 400 ? 75 : 200}
                  plugins={ChartDataLabels}
                  options={{
                    cutoutPercentage: 40,
                    plugins: {
                      legend: {
                        display: false
                      },
                      datalabels: {
                        display: false
                      }
                    },
                    tooltips: {
                      enabled: false,
                    },
                    hover: { mode: null },
                    elements: {
                      arc: {
                        hoverBorderColor: '#E1E5F2',
                        borderColor: '#E1E5F2',
                        borderWidth: 5,
                        width: 50
                      },
                    },
                    animation: {
                      duration: 500,
                    },
                    title: {
                      display: false,
                      text: 'OCUPAÇÃO DE LEITOS',
                    },
                    legend: {
                      display: false,
                      position: 'bottom',
                    },
                    maintainAspectRatio: true,
                    responsive: false,
                  }}
                />
                <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'flex-start', alignItems: 'flex-start' }}>
                  <div className="title2" style={{ color: 'red' }}>
                    {'DESCRIÇÃO DA META...'}
                  </div>
                  <div className="title2">
                    {'VENCIMENTO: 10/11/2021 - TEMPO RESTANTE: 0 DIAS'}
                  </div>
                </div>
              </div>
              <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center' }}>
                <div className="red-button" style={{ width: 200 }}>
                  PENDENTE
                </div>
                <div title="AVALIAÇÃO DO RESULTADO" className="red-button"
                  style={{ padding: 10, width: 50, opacity: 1 }}>
                  0
                </div>
              </div>
            </div>
          </div>
        </div>

        <div className="corprincipal"
          style={{
            width: '100%',
            display: 'flex', flexDirection: 'row', justifyContent: 'flex-start',
            margin: 5, marginBottom: 0,
            padding: 5, borderRadius: 5, borderBottomLeftRadius: 0, borderBottomRightRadius: 0,
          }}>
          <div className="title5" style={{ fontSize: 14, alignSelf: 'center', width: 250 }}>
            OBJETIVO SECUNDÁRIO:
          </div>
          <div className="hover-button" style={{ width: '100%' }}>
            {'DESCRIÇÃO DO OBJETIVO SECUNDÁRIO'}
          </div>
        </div>
        <div id="METAS" className="scroll"
          style={{
            height: 170, margin: 5, marginTop: 0,
            borderTopLeftRadius: 0, borderTopRightRadius: 0,
            backgroundColor: '#ffffff', borderColor: '#ffffff'
          }}>
          <div id="ITEM DE META" className="card" style={{ display: 'flex', flexDirection: 'column', justifyContent: 'flex-start' }}>
            <div
              style={{
                display: 'flex', flexDirection: 'row', width: '100%',
                justifyContent: 'space-between', alignItems: 'center',
              }}>
              <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center', alignItems: 'center' }}>
                <Doughnut
                  data={dataChartMetas3}
                  width={window.innerWidth > 400 ? 75 : 200}
                  height={window.innerWidth > 400 ? 75 : 200}
                  plugins={ChartDataLabels}
                  options={{
                    cutoutPercentage: 40,
                    plugins: {
                      legend: {
                        display: false
                      },
                      datalabels: {
                        display: false
                      }
                    },
                    tooltips: {
                      enabled: false,
                    },
                    hover: { mode: null },
                    elements: {
                      arc: {
                        hoverBorderColor: '#E1E5F2',
                        borderColor: '#E1E5F2',
                        borderWidth: 5,
                        width: 50
                      },
                    },
                    animation: {
                      duration: 500,
                    },
                    title: {
                      display: false,
                      text: 'OCUPAÇÃO DE LEITOS',
                    },
                    legend: {
                      display: false,
                      position: 'bottom',
                    },
                    maintainAspectRatio: true,
                    responsive: false,
                  }}
                />
                <div className="title2">
                  {'DESCRIÇÃO DA META...'}
                </div>
              </div>
              <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center' }}>
                <div className="green-button" style={{ width: 200 }}>
                  EFETIVADA
                </div>
                <div title="AVALIAÇÃO DO RESULTADO" className="green-button"
                  style={{ padding: 10, width: 50, opacity: 1 }}>
                  8
                </div>
              </div>
            </div>
          </div>
        </div>

      </div>
    )
  }

  function PlanoTerapeutico() {
    return (
      <div id="PLANOS" style={{
        display: 'flex', flexDirection: 'column', justifyContent: 'center',
      }}>
        <div id="PLANOS TERAPÊUTICOS" className="title4"
          title="PROPOSTAS TERAPÊUTICAS PARA O ALCANCE DA META"
        >
          PROPOSTAS TERAPÊUTICAS
        </div>
        <div id="terapeutica1"
          className="scroll" style={{ display: 'flex', height: '70vh', margin: 5 }}>
          <div className="card" style={{ flexDirection: 'row', justifyContent: 'space-between' }}>
            <div className="title2" style={{ alignSelf: 'center' }}>PROPOSTA TERAPÊUTICA 01</div>
            <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center' }}>
              <button className="blue-button" style={{ width: 150 }}>INÍCIO</button>
              <button className="blue-button" style={{ width: 150 }}>TÉRMINO</button>
              <button className="green-button" style={{ width: 150 }}>CONCLUÍDA</button>
            </div>
          </div>
        </div>
      </div>
    )
  }

  function EvolucoesDoDia() {
    return (
      <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center' }}>
        <div id="EVOLUCOES" className="title4">EVOLUÇÕES MULTIPROFISIONAIS</div>
        <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center' }}>
          <div id="lista de profissionais" className="scroll"
            style={{ height: '70vh', width: '15vw', margin: 0, marginRight: 5 }}>
            <button className="blue-button">
              MÉDICO
            </button>
            <button className="blue-button">
              ENFERMEIRO
            </button>
            <button className="blue-button">
              FISIO
            </button>
            <button className="blue-button">
              FONO
            </button>
            <button className="blue-button">
              TÉCNICO
            </button>
          </div>
          <div id="view da evolução" className="card" style={{ width: '60vw', height: '70vh', margin: 0 }}>
            DATA E HORA DA ÚLTIMA EVOLUÇÃO DE CADA PROFISSIONAL...
          </div>
        </div>
      </div>
    )
  }

  function SpecialScroll() {
    return (
      <div
        id="specialscroll"
        className="specialscroll"
        //onMouseOver={() => document.getElementById("specialscroll").style.opacity = 1}
        //onMouseOut={() => document.getElementById("specialscroll").style.opacity = 0.2}
        style={{
          width: 10, height: '100vh',
          position: 'absolute', top: '10vh', bottom: 0, right: 25,
          display: 'flex', flexDirection: 'row', justifyContent: 'center',
          alignSelf: 'center', alignItems: 'center',
        }}>
        <div
          className="corprincipal"
          style={{
            width: 10, height: '50%', margin: 0, padding: 0,
            borderRadius: 50, position: 'relative', display: 'flex',
            flexDirection: 'column', justifyContent: 'space-between'
          }}>
          <buttom className="corprincipal" title="CURVA DE MORAES"
            onClick={() => document.getElementById("scroll").scrollTo(0, 0)}
            style={{
              height: 20, minHeight: 20, width: 20, minWidth: 20, borderRadius: 50,
              boxShadow: 'none', marginLeft: -5
            }}>
          </buttom>
          <buttom className="corprincipal" title="OBJETIVOS E METAS"
            onClick={() => {
              var position = document.getElementById("OBJETIVOS").offsetTop;
              document.getElementById("scroll").scrollTo(0, position - 135);
              // alert(position)
            }}
            style={{
              height: 20, minHeight: 20, width: 20, minWidth: 20, borderRadius: 50,
              boxShadow: 'none', marginLeft: -5
            }}>
          </buttom>
          <buttom className="corprincipal" title="PLANOS TERAPÊUTICOS"
            onClick={() => {
              var position = document.getElementById("PLANOS TERAPÊUTICOS").offsetTop;
              document.getElementById("scroll").scrollTo(0, position - 135);
            }}
            style={{
              height: 20, minHeight: 20, width: 20, minWidth: 20, borderRadius: 50,
              boxShadow: 'none', marginLeft: -5
            }}>
          </buttom>
          <buttom className="corprincipal" title="EVOLUÇÕES MULTIPROFISSIONAIS"
            onClick={() => {
              var position = document.getElementById("EVOLUCOES").offsetTop;
              document.getElementById("scroll").scrollTo(0, position - 135);
            }}
            style={{
              height: 20, minHeight: 20, width: 20, minWidth: 20, borderRadius: 50,
              boxShadow: 'none', marginLeft: -5
            }}>
          </buttom>
        </div>
      </div>
    )
  }

  return (
    <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center', height: '100%' }}>
      <SpecialScroll></SpecialScroll>
      <div
        id="scroll"
        className="scroll"
        style={{
          scrollBehavior: 'smooth',
          width: '82vw',
          height: '100%', display: 'flex', flexDirection: 'column', justifyContent: 'flex-start',
          backgroundColor: 'transparent', borderColor: 'transparent',
          paddingRight: 35,
        }}>
        <Regua></Regua>
        <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center' }}>
          <AvaliacaoDeDeclinioFuncional></AvaliacaoDeDeclinioFuncional>
          <ClassificacaoDeRiscoFuncional></ClassificacaoDeRiscoFuncional>
        </div>
        <DeterminantesDoDeclinioFuncionalEstabelecido></DeterminantesDoDeclinioFuncionalEstabelecido>
        <LinhaDeCuidados></LinhaDeCuidados>
        <MetasTerapeuticas></MetasTerapeuticas>
        <PlanoTerapeutico></PlanoTerapeutico>
        <EvolucoesDoDia></EvolucoesDoDia>
      </div>
    </div>
  )
}

export default AptPlanoTerapeutico;